      "M\u00ec T\u00e0u ch\u00ednh c\u1ed1ng l\u00e0 m\u1ea5y ti\u1ec7m m\u00ec, xe m\u00ec c\u00f3 ch\u1eef k\u00fd ph\u00eda sau. Th\u00ed d\u1ee5 nh\u01b0 T\u00e0i K\u00fd, Minh K\u00fd, T\u00e2n K\u00fd ... M\u1ea7y \u0111\u1ec3 \u00fd coi, ti\u1ec7m n\u00e0o c\u0169ng c\u00f3 ch\u1eef k\u00fd h\u1ebft. C\u00f3 ch\u1eef k\u00fd m\u1edbi ngon.",
