    quote:
      "Some very common foods and drinks are aversive. Few people enjoy, at first, coffee, beer, tobacco, or chili pepper. Pleasure from pain is uniquely human. No other animal willingly eats such foods when there are alternatives. Philosophers have often looked for the defining feature of humans\u2014language, rationality, culture, and so on. I'd stick with this: Man is the only animal that likes Tabasco sauce.",
    author:
      "Paul Bloom,  How Pleasure Works: The New Science of Why We Like What We Like",
